import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GoogleDriveProviderResult, IClearGoogleDriveData, IEntryGoogleDriveData } from '@shared/models/google-drive-provider.models';
import { environment } from '@environments/environment';
import { EMPTY, forkJoin, from, Observable } from 'rxjs';
import { IExperience, IExperienceItem } from '@shared/models/experience-model';
import { ISEOMetaItem, IStaticPage, IStaticPageItem } from '@shared/models/s-e-o-model';
import { catchError, retry, shareReplay } from 'rxjs/operators';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class GoogleDriveProvider {
  private cachedData: any;
  private finalData: Promise<GoogleDriveProviderResult>;
  private spreadSheetEntry: IEntryGoogleDriveData[];
  private readonly spreadSheetId: string;
  private sheetIdList: string[];

  constructor(
    public http: HttpClient
  ) {
    this.spreadSheetId = environment.spreadSheetId;
  }

  public load() {
    if (!this.finalData) {
      this.finalData = this.runCallsSequence();
    }
    return from(this.finalData);
  }

  private async runCallsSequence(): Promise<GoogleDriveProviderResult> {
    await this.getSheetIdList();
    await this.getSheetList();
    return {
      experienceList$: from(this.getExperienceList()),
      staticPageList$: from(this.getStaticPageList())
    };
  }

  private async getSheetIdList(): Promise<void> {
    const spreadSheetUrl = 'https://spreadsheets.google.com/feeds/worksheets/' + this.spreadSheetId + '/public/values?alt=json';
    const data: IClearGoogleDriveData = await this.http.get<IClearGoogleDriveData>(spreadSheetUrl).toPromise();
    this.sheetIdList = data.feed.entry.map(item => {
      const urlParts = item.id.$t.split('/');
      return urlParts[urlParts.length - 1];
    });
  }

  private async getSheetList(): Promise<void> {
    const returnArray: { [key: string]: {}[] } = {};
    const sheetRequestsList: Observable<IClearGoogleDriveData>[] = [];

    this.sheetIdList.forEach(sheetId => {
      const sheetUrl = 'https://spreadsheets.google.com/feeds/list/' + this.spreadSheetId + '/' + sheetId + '/public/values?alt=json';
      sheetRequestsList.push(
        this.http.get<IClearGoogleDriveData>(sheetUrl).pipe(
          retry(3),
          catchError(() => {return EMPTY}),
          shareReplay()
        )
      );
    });

    const data = await forkJoin(sheetRequestsList).toPromise();
    data.forEach(data => {
      this.spreadSheetEntry = data.feed.entry;
      returnArray[data.feed.title.$t] = [];
      if (this.spreadSheetEntry && this.spreadSheetEntry.length > 0) {
        this.spreadSheetEntry.forEach((entry, index) => {
          const obj = {};
          for (let x in entry) {
            if (x.includes('gsx$') && entry[x].$t) {
              obj[x.split('$')[1]] = entry[x]['$t'];
            }
          }
          returnArray[data.feed.title.$t].push(obj);
        });
      }
    });
    this.cachedData = returnArray;
  }

  private async getExperienceList(): Promise<IExperience> {
    return {
      experienceList: this.cachedData['experience-main']
        .map((item: IExperienceItem) => {
          const projectDateRangeFrom = new Date(this.cachedData['experience-date']
            .filter(x => x['experience-main-id'] == item['id'])
            .find(x => x['catalog-date-type-id'] == this.cachedData['catalog-date-type']
                .find(x => x['name'] == 'start')['id']
            )['date'])
          const projectDateRangeTo = new Date(this.cachedData['experience-date']
            .filter(x => x['experience-main-id'] == item['id'])
            .find(x => x['catalog-date-type-id'] == this.cachedData['catalog-date-type']
                .find(x => x['name'] == 'end')['id']
            )['date'])
          let company = this.cachedData['experience-company']
            .map(item => {
              item.avatarPath = item['avatar-path']
              return item
            })
            .find(x => x['id'] == item['experience-company-id'])

          return {
            id: item['id'],
            company: company,
            systemName: item['system-name'],
            position: item['position'],
            customerName: item['customer'],
            roleDescription: item['role'],
            duties: item['duties'],
            projectDateRange: {
              from: projectDateRangeFrom,
              to: projectDateRangeTo,
            },
            technologyList: {
              frameworkList: this.cachedData['experience-technologies']
                .filter(x => x['experience-main-id'] == item['id'])
                .filter(x => this.cachedData['catalog-technologies']
                  .filter(x => x['catalog-technologies-type-id'] == this.cachedData['catalog-technologies-type']
                    .find(x => x['name'] == 'frameworks')['id'])
                  .map(catalogTechnologiesData => {
                    return catalogTechnologiesData['id'];
                  })
                  .includes(x['catalog-technologies-id']))
                .map(experienceTechnologiesData => {
                  return {
                    id: experienceTechnologiesData['id'],
                    name: this.cachedData['catalog-technologies']
                      .find(x => x['id'] == experienceTechnologiesData['catalog-technologies-id'])['name'],
                    version: experienceTechnologiesData['version']
                  };
                }),
              other: this.cachedData['experience-technologies']
                .filter(x => x['experience-main-id'] == item['id'])
                .filter(x => this.cachedData['catalog-technologies']
                  .filter(x => x['catalog-technologies-type-id'] == this.cachedData['catalog-technologies-type']
                    .find(x => x['name'] == 'other')['id'])
                  .map(catalogTechnologiesData => {
                    return catalogTechnologiesData['id'];
                  })
                  .includes(x['catalog-technologies-id']))
                .map(experienceTechnologiesData => {
                  return {
                    id: experienceTechnologiesData['id'],
                    name: this.cachedData['catalog-technologies']
                      .find(x => x['id'] == experienceTechnologiesData['catalog-technologies-id'])['name'],
                    version: experienceTechnologiesData['version']
                  };
                }),
              pipelineList: this.cachedData['experience-technologies']
                .filter(x => x['experience-main-id'] == item['id'])
                .filter(x => this.cachedData['catalog-technologies']
                  .filter(x => x['catalog-technologies-type-id'] == this.cachedData['catalog-technologies-type']
                    .find(x => x['name'] == 'pipelines')['id'])
                  .map(catalogTechnologiesData => {
                    return catalogTechnologiesData['id'];
                  })
                  .includes(x['catalog-technologies-id']))
                .map(experienceTechnologiesData => {
                  return {
                    id: experienceTechnologiesData['id'],
                    name: this.cachedData['catalog-technologies']
                      .find(x => x['id'] == experienceTechnologiesData['catalog-technologies-id'])['name'],
                    version: experienceTechnologiesData['version']
                  };
                }),
              programLanguageList: this.cachedData['experience-technologies']
                .filter(x => x['experience-main-id'] == item['id'])
                .filter(x => this.cachedData['catalog-technologies']
                  .filter(x => x['catalog-technologies-type-id'] == this.cachedData['catalog-technologies-type']
                    .find(x => x['name'] == 'program_languages')['id'])
                  .map(catalogTechnologiesData => {
                    return catalogTechnologiesData['id'];
                  })
                  .includes(x['catalog-technologies-id']))
                .map(experienceTechnologiesData => {
                  return {
                    id: experienceTechnologiesData['id'],
                    name: this.cachedData['catalog-technologies']
                      .find(x => x['id'] == experienceTechnologiesData['catalog-technologies-id'])['name'],
                    version: experienceTechnologiesData['version']
                  };
                }),
              versionControlSystem: this.cachedData['experience-technologies']
                .filter(x => x['experience-main-id'] == item['id'])
                .filter(x => this.cachedData['catalog-technologies']
                  .filter(x => x['catalog-technologies-type-id'] == this.cachedData['catalog-technologies-type']
                    .find(x => x['name'] == 'vcs')['id'])
                  .map(catalogTechnologiesData => {
                    return catalogTechnologiesData['id'];
                  })
                  .includes(x['catalog-technologies-id']))
                .map(experienceTechnologiesData => {
                    return {
                      id: experienceTechnologiesData['id'],
                      name: this.cachedData['catalog-technologies']
                        .find(x => x['id'] == experienceTechnologiesData['catalog-technologies-id'])['name'],
                      version: experienceTechnologiesData['version']
                    };
                  }
                )
              }
            };
          }
        )
    };
  }

  private async getStaticPageList(): Promise<IStaticPage> {
    return {
      pageList: this.cachedData['static-page']
        .map((data: IStaticPageItem) => {
            return <IStaticPageItem> {
              id: data['id'],
              name: data['name'],
              seoMeta: this.cachedData['seo-meta-main']
                .filter(item => item['id'] == data['seo-meta-main-id'])
                .map((data: ISEOMetaItem) => {
                    return {
                      id: data['id'],
                      title: data['title'],
                      description: data['description'],
                      keywords: this.cachedData['seo-meta-keyword']
                        .filter(item => item['seo-meta-main-id'] == data['id'])
                        .map(item => item['name']),
                      author: {
                        firstName: data['author-first-name'],
                        lastName: data['author-last-name'],
                        email: data['author-email'],
                      },
                      robots: {
                        indexing: data['robots-indexing'],
                        following: data['robots-following'],
                      },
                      language: {
                        languageCode: data['language-language-code'],
                        countryCode: data['language-country-code'],
                      },
                      dates: {
                        publishedDate: new Date(data['dates-published-date']),
                        modifiedDate: data['dates-modified-date'] ? new Date(data['dates-modified-date']) : null,
                        expirationDate: data['dates-expiration-date'] ? new Date(data['dates-expiration-date']) : null,
                      }
                    };
                  }
                )[0]
            };
          }
        )
    };
  }
}
