  private setJsonLdWebSite() {
    if (!!this.nodeWebSiteScript) {
      this.renderer2.removeChild(this.document.head, this.nodeWebSiteScript)
    }
    this.nodeWebSiteScript = this.renderer2.createElement('script');
    this.nodeWebSiteScript['type'] = 'application/ld+json';
    this.nodeWebSiteScript['text'] = `${JSON.stringify({
      "@context": "http://schema.org",
      "@type": "WebSite",
      "name": "Markel Kalugin | Senior Software Developer",
      "url": `${this.siteURL}`,
    })}`
    this.renderer2.appendChild(this.document.head, this.nodeWebSiteScript)
  }

