    this.title.setTitle(meta.title);
    this.meta.updateTag({name: "description", content: meta.description});
    this.meta.updateTag({name: "keywords", content: meta.keywords.join(',')});
    this.meta.updateTag({name: "author", content: `${meta.author.firstName} ${meta.author.lastName}`});
    this.meta.updateTag({name: "copyright", content: meta.title});
    this.meta.updateTag({name: "publisher-email", content: meta.author.email});
    this.meta.updateTag({name: "publisher-url", content: this.siteURL});
