    this.meta.updateTag({name: "p:domain_verify", content: "48e836c7939a77be6ec48f998d7858d9"});
    // <!--  Markup for Facebook  --}
    this.meta.updateTag({property: "og:title", content: meta.title});
    this.meta.updateTag({property: "og:type", content: "website"});
    this.meta.updateTag({property: "og:url", content: this.siteURL});
    this.meta.updateTag({property: "og:site_name", content: "Senior Python Software Engineer"});
    this.meta.updateTag({property: "og:description", content: meta.description});
    this.meta.updateTag({property: "og:determiner", content: "a"});
    this.meta.updateTag({property: "og:locale", content: `${meta.language.languageCode}_${meta.language.countryCode}`});
    this.meta.updateTag({property: "og:image", content: `${this.siteURL}assets/avatar.jpg`});
    this.meta.updateTag({property: "og:image:url", content: "/assets/avatar.jpg"});
    this.meta.updateTag({property: "og:image:secure_url", content: `${this.siteURL}assets/avatar.jpg`});
    this.meta.updateTag({property: "og:image:type", content: "image/jpeg"});
    this.meta.updateTag({property: "og:image:width", content: "960"});
    this.meta.updateTag({property: "og:image:height", content: "960"});
    this.meta.updateTag({property: "article:author", content: `${meta.author.firstName} ${meta.author.lastName}`});
    this.meta.updateTag({property: "article:section", content: "Landing"});
    this.meta.updateTag({property: "article:tag", content: meta.keywords.join(' ')});
    this.meta.updateTag({property: "article:published_time", content: meta.dates.publishedDate.toISOString()});
    this.meta.updateTag({property: "article:modified_time", content: meta.dates.modifiedDate.toISOString()});
    // <!--    this.meta.updateTag({property: "article:expiration_time", content: "2019-09-24T22:08:46+03:00" /}--}
    this.meta.updateTag({property: "profile:first_name", content: meta.author.firstName});
