import { Component, Input, OnInit, Renderer2 } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { from, Subject } from 'rxjs';
import { distinctUntilChanged, filter } from 'rxjs/operators';
import { SEOService } from '@shared/services/s-e-o.service';
import { BreadCrumbItem } from '@shared/models/s-e-o-model';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.sass']
})
export class HeaderComponent implements OnInit {

  @Input('genericStyleClass') public genericStyleClass: string = 'general-header';

  public breadCrumbsItemList$: Subject<BreadCrumbItem[]> = new Subject();

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private seoService: SEOService,
    private renderer2: Renderer2,
  ) {
  }

  ngOnInit(): void {
    this.getBreadCrumbs();
    this.seoService.setRender2(this.renderer2)
    this.breadCrumbsItemList$.subscribe(data => {
      this.seoService.setJsonLdBreadcrumbList(data)
    })
  }

  public getBreadCrumbs(): void {
    from(this.getChildrenTree()).subscribe(data => {
      this.breadCrumbsItemList$.next(data);
    });
    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        distinctUntilChanged())
      .subscribe(route => {
      from(this.getChildrenTree()).subscribe(data => {
        this.breadCrumbsItemList$.next(data)
      });
    });
  }

  private async getChildrenTree(
    activatedRoute: ActivatedRoute = null,
    childrenList: BreadCrumbItem[] = [{
      url: [''],
      label: 'Home'
    }]
  ): Promise<BreadCrumbItem[]> {
    const route = !activatedRoute ? this.activatedRoute.root.firstChild.firstChild : activatedRoute.firstChild;
    if (!!route && !!route.routeConfig) {
      const item = {
        url: childrenList.map(item => item.url[item.url.length - 1]).concat(HeaderComponent.getRouteURL(route)),
        label: HeaderComponent.getRouteLabel(route)
      };
      childrenList.push(item);
      return await this.getChildrenTree(route, childrenList);
    }
    return childrenList;
  }

  private static getRouteURL(route: ActivatedRoute): string {
    let result: string;
    if (Object.keys(route.params['_value']).length > 0) {
      result = route.params['_value'][route.routeConfig.data.breadCrumbItem.labelName];
    } else if (route.routeConfig.path === '') {
      result = route.routeConfig.data.breadCrumbItem.labelName.toLowerCase();
    } else {
      result = route.routeConfig.path;
    }
    return result;
  }

  private static getRouteLabel(route: ActivatedRoute): string {
    let result: string;
    if (Object.keys(route.params['_value']).length > 0) {
      result = route.params['_value'][route.routeConfig.data.breadCrumbItem.labelName];
    } else {
      result = route.routeConfig.data.breadCrumbItem.labelName;
    }
    return result
  }

}
